*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    width: 1000px !important;
}
::-webkit-scrollbar{
    background:pink;
}
body{
    width: 100%;
}
@media (max-width:500px){
     header{
         overflow-x: hidden !important;
     }
     section{
         overflow-x: hidden;
     }
     .glav{
         overflow-x: hidden;
     }
     .overall{
         overflow-x: hidden;
     }
     aside{
         overflow-x: hidden;
     }
}